

































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
// import { recommendList } from '../../service/getData';

// //MD5加密
// export const md5Encrypt = (encryptString) => {
//     let md5 = crypto.createHash('md5').update(encryptString).digest('hex');
//     return md5;
// };

@Component({
  components: {},
  mixins: [mixin]
})
export default class shopIndex extends Vue {
  loginName = "";
  password = "";
  @Action("setTokenInfo") setTokenInfo;

  clearLoginName() {
    //  this.loginName = ""
    console.log(111);
  }
  doLogin() {
    if (this.loginName == "" || this.password == "") {
      return;
    }
    Vue.prototype.$reqFormPost(
      "/auth/user/login",
      {
        loginName: this.loginName,
        password: this.password
      },
      // md5
      // require('crypto').createHash('md5').update(this.password).digest('hex')
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        console.log(res);

        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }

        this.setTokenInfo(res.data.data);
        localStorage.setItem(
          Vue.prototype.MutationTreeType.TOKEN_INFO,
          JSON.stringify(res.data.data)
        );
        this.$router.push("/");
      }
    );
  }
  gosign() {
    this.$router.push("/sign");
  }
  goforget() {
    this.$router.push("/forget");
  }
  goback() {
    this.$router.go(-1);
  }
  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }
  mounted() {
    
  }
}



